@tailwind base;
@tailwind components;
@tailwind utilities;

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.color{
    background: rgb(19,15,44);
background: radial-gradient(circle, rgba(19,15,44,1) 0%, rgba(28,32,76,1) 180%);
}

.borderAdd{
    box-shadow: 4px 4px 50px white;
}

.active{
    cursor:default;
}

input{
    all: unset;
    min-width: 300px;
    background-color: white;
    opacity: 80%;
    border-radius: 5px;
    color: rgb(46, 41, 41);
    padding: 8px;
    font-size: 24px;
}

input::placeholder{
    all: unset;
    padding: 5px;
    background-color: white;
    opacity: 80%;
    border-radius: 5px;
}

.sign-up{
    background: linear-gradient(90deg, #FFF -2.94%, rgba(255, 255, 255, 0.00) 101.9%);
}